const validates = {
	required: function(v) {
		return v.toString().trim().length > 0 ? true : ""
	},
	tel: function(v) {
		return /^1\d{10}$/.test(v) ? true : ""
	},
	gtZero: function(v) {
		return v > 0 ? true : " 0"
	},
	gtOrEqualZero: function(v) {
		return v >= 0 ? true : " 0"
	}
};
export default validates