<template>
	<v-form v-model="valid" ref="form">
		<v-toolbar fixed dark color="primary">
			<v-btn icon @click="$router.go(-1)">
				<v-icon>arrow_back</v-icon>
			</v-btn>

			<v-toolbar-title></v-toolbar-title>

			<v-spacer></v-spacer>
			<v-btn icon>
				<v-icon>delete</v-icon>
			</v-btn>
			<v-btn type="submit" @click.prevent="submit" icon>
				<v-icon>save</v-icon>
			</v-btn>
		</v-toolbar>
		<div class="header-padding"></div>
		<v-card>
			<FileSelector accept="image/*" :value="values.image" @input="onImageChange" placeholder="">
				<v-img :src="values.image" aspect-ratio="2.75"></v-img>
			</FileSelector>

			<v-container grid-list-xl text-xs-center>
				<v-text-field v-model="values.name" label="" required :rules="rules.name" validate-on-blur></v-text-field>
				<v-layout row wrap>
					<v-flex xs6>
						<v-text-field v-model="values.origin" label="" required :rules="rules.origin" validate-on-blur></v-text-field>
					</v-flex>
					<v-flex xs6>
						<v-text-field v-model="values.widget" label="" required :rules="rules.widget" validate-on-blur></v-text-field>
					</v-flex>
				</v-layout>
			</v-container>
		</v-card>
		<v-card>
			<v-container grid-list-xl text-xs-center>
				<v-layout row wrap>
					<v-flex xs6>
						<v-text-field type="number" min="0" v-model.number="values.costprice" label="" required :rules="rules.costprice" validate-on-blur></v-text-field>
					</v-flex>
					<v-flex xs6>
						<v-text-field type="number" v-model.number="values.price" label="" required :rules="rules.price" validate-on-blur></v-text-field>
					</v-flex>
					<v-flex xs6>
						<v-checkbox label="" v-model="values.active"></v-checkbox>
					</v-flex>
					<v-flex xs6>
						<v-text-field v-model="values.spec" label="" required :rules="rules.spec" validate-on-blur></v-text-field>
					</v-flex>
				</v-layout>
				<v-text-field v-model="values.logistics" label="" required :rules="rules.logistics" validate-on-blur></v-text-field>
			</v-container>
		</v-card>
		<v-dialog v-model="dialogOptions.show" persistent max-width="290">
			<v-card>
				<v-card-text>{{dialogOptions.content}}</v-card-text>
				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn flat v-for="(btn,index) in dialogOptions.btns" :key="index" :color="btn.color" @click="dialogBtnClick(btn)">{{btn.text}}</v-btn>
				</v-card-actions>
			</v-card>
		</v-dialog>
	</v-form>
</template>

<script>
	import { mapActions,mapGetters } from 'vuex'
	import FileSelector from '../components/FileSelector.vue'
	import validates from '../libs/validates'
	import getImageBase64 from '../libs/getImageBase64'

	import DialogMixins from '../mixins/DialogMixins'
	import ViewMixins from '../mixins/ViewMixins'
	export default {
		components: {
			FileSelector
		},
		mixins: [DialogMixins,ViewMixins],
		activated() {
			this.fetchData()
		},
		beforeRouteLeave(to, from, next) {
			if(!this.$refs.form.validate()) {
				next(false);
				this.openDialog({
					content: "?"
				}).then(e => {
					e.role == "ok" ? next() : next(false)
				})
			} else {
				next()
			}
		},
		data() {
			return {
				valid: true,
				rules: {
					name: [
						validates.required
					],
					origin: [
						validates.required
					],
					widget: [
						validates.required
					],
					spec: [
						validates.required
					],
					costprice: [
						validates.required,
						validates.gtZero
					],
					price: [
						validates.required,
						validates.gtOrEqualZero
					],
					logistics: [
						validates.required
					]
				},
				values: {
					gid: "",
					gtid:"",
					image: "",
					name: "",
					origin: "",
					widget: "",
					spec: "",
					shelflife:-1,
					costprice: 1,
					price: 1,
					logistics: "",
					sort: 0,
					active: true,
					delete:false,
					remark:null,
					createtime: Date.now(),
					modifytime: null,
					json:null
				}
			}
		},
		computed:mapGetters("goods",['get']),
		methods: {
			...mapActions('goods',['put']),
			onImageChange(files) {
				Promise.all(files.map(file => getImageBase64(file, 400))).then(results => {
					this.values.image = results[0];
				})
			},
			fetchData() {
				Object.assign(this.values, this.get(this.$route.params.gid))
			},
			submit() {
				if(this.$refs.form.validate()) {
					this.put( Object.assign({}, this.values)).then(() => {
						return history.back()
					})
				}
			}
		}
	}
</script>

<style scoped="scoped">	
	.v-card {
		margin: 8px;
	}
</style>