<template>
	<div>
		<v-toolbar fixed dark color="primary">
			<v-btn icon @click="$router.go(-1)">
				<v-icon>arrow_back</v-icon>
			</v-btn>

			<v-toolbar-title></v-toolbar-title>
		</v-toolbar>
		<div class="header-padding"></div>
		<v-card>
			<v-container grid-list-xl text-xs-center>
				<v-select v-model="values.template" :items="templateItems" label=""></v-select>
				<v-slider @touchend.native.stop v-model="values.quality" min="35" max="100" step="5" tick-size="3" thumb-size="28" label="" hide-details ticks thumb-label="always"></v-slider>
				<v-slider @touchend.native.stop v-model="values.scale" min="20" max="100" step="5" tick-size="3" thumb-size="28" label="" hide-details ticks thumb-label="always"></v-slider>
			</v-container>
		</v-card>
		<div class="box" ref="box">
			<div class="price-list" ref="list">
				<div class="template1" v-for="(item,index) in list">
					<img :src="item.image" />
					<div class="itemcontent">
						<div class="text name-row"><span><span class="name">{{item.name}}</span> ({{item.origin}}) {{item.widget}}</span>
						</div>
						<div class="text logistics-row"><span>{{item.logistics}}</span></div>
						<div class="text price-row"><span class="price">{{item.price}}</span>&emsp;<span>({{item.spec}})</span></div>
					</div>
				</div>
			</div>
		</div>
		<v-btn fixed dark fab bottom right color="pink" @click="download" style="bottom:60px">
			<v-icon>arrow_downward</v-icon>
		</v-btn>
		<div style="height: 0; overflow: hidden;">
			<canvas ref="canvas"></canvas>
		</div>
	</div>
</template>

<script>
	import db from '../db/index'
	import eventbus from '../libs/eventbus'
	import download from '../libs/download'
	import html2canvas from 'html2canvas'
	export default {
		activated() {
			this.fetchData()
		},
		data() {
			return {
				templateItems: [""],
				list: [],
				values: {
					template: "",
					scale: 100,
					quality: 100
				}
			}
		},
		methods: {
			fetchData() {
				this.list = this.$route.params.data;
			},
			scaleUI() {
				function eachSize(item, fontSize) {
					if(item.scrollWidth > item.clientWidth) {
						fontSize--;
						item.firstElementChild.style.fontSize = fontSize + 'px';
						eachSize(item, fontSize)
					}
				}
				this.$nextTick(() => {
					if(this.$refs.list.clientWidth) {
						[].slice.call(this.$refs.list.querySelectorAll(".text")).forEach((item) => {
							eachSize(item, parseInt(getComputedStyle(item.firstElementChild).fontSize))
						});
						this.$refs.box.style.height = this.$refs.list.getBoundingClientRect().height + 'px'
					}
				})
			},
			download() {
				html2canvas(this.$refs.list, {
					canvas: this.$refs.canvas
				}).then(canvas => {
					const devicePixelRatio = window.devicePixelRatio || 1,
						context = canvas.getContext("2d"),
						imgWidth = canvas.width / 2,
						imgHeight = canvas.height / 2,
						imageData = context.getImageData(0, 0, imgWidth, imgHeight);
					let width = imgWidth,
						height = imgHeight;

					canvas.width = width;
					canvas.height = height;

					if(this.values.scale !== 100) {
						width = Math.floor(imgWidth * this.values.scale / 100);
						height = Math.floor(imgHeight * this.values.scale / 100);

						const canvas2 = document.createElement("canvas"),
							context2 = canvas2.getContext('2d');
						canvas2.width = imgWidth;
						canvas2.height = imgHeight;
						context2.putImageData(imageData, 0, 0);

						canvas.width = width;
						canvas.height = height;
						context.drawImage(canvas2, 0, 0, imgWidth, imgHeight, 0, 0, width, height);
					} else {
						context.putImageData(imageData, 0, 0)
					}

					canvas.style.width = width / devicePixelRatio + 'px';
					canvas.style.height = height / devicePixelRatio + 'px';
					
					download(canvas.toDataURL("image/jpeg", this.values.quality / 100).split(',')[1],"image/jpeg",`${new Date().toJSON()}.jpg`)
//var imgdata=canvas.toDataURL("image/jpeg", this.values.quality / 100);
//this.$refs.download.href=imgdata.replace("image/jpeg","image/octet-stream");
//debugger;
//					canvas.toBlob((blob) => {
//						const url = URL.createObjectURL(blob);
//						this.$refs.download.href=url;
//						//download(url, `${new Date().toJSON()}.jpg`);
//						//URL.revokeObjectURL(url)
//					}, "image/jpeg", this.values.quality / 100)
					//window.location=canvas.toDataURL("image/jpeg", this.values.quality / 100)
				})
			}
		}
	}
</script>

<style lang="stylus" scoped="scoped">
	.v-card {
		margin: 8px;
	}
	
	.box {
		overflow: hidden;
	}
	
	.price-list {
		width: 200%;
		transform: scale(0.5);
		transform-origin: top left;
	}
	
	.text {
		white-space: nowrap;
		width: 100%;
		overflow: hidden;
	}
	
	.template1 {
		padding: 4px 16px;
		display: flex;
		align-items: center;
		img {
			position: relative;
			z-index: 1;
			width: 160px;
			height: 160px;
			border-radius: 100px;
			border: 4px solid #fff;
		}
		.itemcontent {
			//flex: 1 1 auto;
			overflow: hidden;
			//height: 80px;
			border-radius: 10px;
			background-color: #f1f1f1;
			width: 100%;
			margin-left: -60px;
			padding-left: 72px;
			box-sizing: content-box;
			padding-top: 4px;
		}
		.name-row {
			font-weight: bold;
			font-size: 30px;
			.name {
				font-size: 36px;
			}
		}
		.logistics-row {
			color: #aaa;
			font-size: 24px;
		}
		.price-row {
			font-weight: bold;
			color: red;
			font-size: 28px;
			.price {
				font-size: 36px;
			}
		}
	}
</style>