<template>
	<label>
		<input type="file" :accept="accept" :multiple="multiple" @change="change" ref="input" />
		<slot></slot>
		<div class="placeholder" v-if="!value">
			{{placeholder}}
		</div>
	</label>
</template>

<script>
	export default {
		props: {
			value: "",
			accept: "",
			multiple: "",
			placeholder: ""
		},
		methods: {
			change(e) {
				this.$emit("input", [].slice.call(e.target.files));
				this.$refs.input.value = null;
			}
		}
	}
</script>

<style scoped="scoped">
	label {
		position: relative;
		display: block;
		overflow: hidden;
		background-color: #eee;
	}
	
	input {
		border: none;
		opacity: 0.0.1;
		position: absolute;
		top: -50px;
		width: 1px;
		height: 1px;
	}
	
	.placeholder {
		text-align: center;
		position: absolute;
		z-index: 1;
		left: 0;
		right: 0;
		margin-top: -2em;
		text-align: center;
		color: #999;
	}
</style>