function getDefaultDialogOptions() {
	return {
		show: false,
		title: "",
		content: "",
		btns: [{
			role: "cancel",
			color: "green darken-1",
			text: ""
		}, {
			role: "ok",
			color: "blue darken-1",
			text: ""
		}]
	}
};
export default {
	data() {
		return {
			dialogOptions: getDefaultDialogOptions()
		}
	},
	methods: {
		openDialog( options={}) {
			return this.dialogPromise= new Promise((resolve) => {
				options.show = true;
				this.dialogResolve = resolve;
				this.dialogOptions = Object.assign(getDefaultDialogOptions(), options);
			})
		},
		dialogBtnClick(e) {
			this.dialogResolve(e);
			this.dialogPromise.then((e) => {
				if(e) {
					this.dialogOptions.show = false;
				}
			})
		}
	}
}