<template>
	<v-form v-model="valid" ref="form">
		<v-toolbar fixed dark color="primary">
			<v-btn icon @click="$router.go(-1)">
				<v-icon>arrow_back</v-icon>
			</v-btn>

			<v-toolbar-title></v-toolbar-title>

			<v-spacer></v-spacer>
			<v-btn icon>
				<v-icon>delete</v-icon>
			</v-btn>
			<v-btn type="submit" @click.prevent="submit" icon>
				<v-icon>save</v-icon>
			</v-btn>
		</v-toolbar>
		<div class="header-padding"></div>
		<v-card>
			<v-img :src="goods.image" aspect-ratio="2.75"></v-img>
			<v-container grid-list-xl text-xs-center>
				<v-text-field disabled v-model="goods.name" label="" hide-details></v-text-field>
				<v-layout row wrap>
					<v-flex xs6>
						<v-text-field disabled v-model="values.costprice" label="" hide-details></v-text-field>
					</v-flex>
					<v-flex xs6>
						<v-text-field disabled v-model="values.price" label="" hide-details></v-text-field>
					</v-flex>
					<v-flex xs6>
						<v-text-field v-model="values.offer" label="" min="0"></v-text-field>
					</v-flex>
					<v-flex xs6>
						<v-checkbox label="" v-model="values.income"></v-checkbox>
					</v-flex>
				</v-layout>
			</v-container>
		</v-card>
		<v-card>
			<v-container grid-list-xl text-xs-center>
				<v-layout row wrap>
					<v-flex xs6>
						<v-text-field type="date" v-model="values.billtime" label="" required ></v-text-field>
					</v-flex>
				</v-layout>
			</v-container>
		</v-card>
		<v-dialog v-model="dialogOptions.show" persistent max-width="290">
			<v-card>
				<v-card-text>{{dialogOptions.content}}</v-card-text>
				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn flat v-for="(btn,index) in dialogOptions.btns" :key="index" :color="btn.color" @click="dialogBtnClick(btn)">{{btn.text}}</v-btn>
				</v-card-actions>
			</v-card>
		</v-dialog>
	</v-form>
</template>

<script>
	import { mapActions, mapGetters } from 'vuex'
	import DialogMixins from '../mixins/DialogMixins'
	import ViewMixins from '../mixins/ViewMixins'

	import { dateFormat } from '../libs/utils'

	export default {
		mixins: [DialogMixins, ViewMixins],
		activated() {
			this.fetchData()
		},
		beforeRouteLeave(to, from, next) {
			if(!this.$refs.form.validate()) {
				next(false);
				this.openDialog({
					content: "?"
				}).then(e => {
					e.role == "ok" ? next() : next(false)
				})
			} else {
				next()
			}
		},
		data() {
			return {
				valid:true,
				goods: {
					image: "",
					name: "",
					origin: ""
				},
				customer: {
					name: "",
					wx: "",
					qq: "",
					tel: ""
				},
				receiver: {
					name: "",
					tel: "",
					address: ""
				},
				values: {
					bid: "",
					gid: "",
					cid: "",
					rid: "",
					aid: "",
					costprice: 1,
					price: 0,
					widget: "",
					spec: "",
					profit: 0,
					offer: 0,
					//
					income: true,
					remark: "",
					billtime: dateFormat(Date.now(), 'yyyy-MM-dd'),
					modifytime: null,
					createtime: Date.now(),
					json: null
				}
			}
		},
		computed: mapGetters("goods", {
			getGoods: 'get'
		}),
		methods: {
			fetchData() {
				if(this.$route.params.gid) {
					const goods = this.getGoods(this.$route.params.gid)
					Object.assign(this.goods, goods);
					["gid", "constprice", "price", "widget", "spec"].forEach((key) => {
						this.values[key] = goods[key]
					})
				} else if(this.$route.params.bid) {
					const bill = this.get(this.$route.params.bid);
					Object.assign(this.goods, this.getGoods(bill.gid));
					Object.assign(this.customer, this.getCustomer(bill.cid));
					Object.assign(this.receiver, this.getReceiver(bill.rid));
				}
			},
			submit() {

			}
		}
	}
</script>

<style lang="stylus" scoped="scoped">
	.v-card {
		margin: 8px;
	}
</style>