const canvas = document.createElement("canvas");
const context = canvas.getContext("2d");
export default function(file, size = 400) {
	//document.body.appendChild(canvas);
	return new Promise((resolve, reject) => {
		const url = URL.createObjectURL(file);
		const img = new Image();
		img.src = url;
		img.onload = function() {

			context.clearRect(0, 0, canvas.width, canvas.height);
			size = Math.min(size, img.width, img.height);

			canvas.width = size;
			canvas.height = size;

			const move = Math.ceil((img.width - img.height) / 2),
				ssize = Math.min(img.width, img.height);
			context.drawImage(img, move > 0 ? move : 0, move < 0 ? -move : 0, ssize, ssize, 0, 0, size, size);
			resolve(canvas.toDataURL())
		}
		img.onerror = function(e) {
			reject(e)
		}
	})

}