<template>
	<v-tabs fixed-tabs v-model="active">
		<v-tab-item id="tab1">
			<Home></Home>
		</v-tab-item>
		<v-tab-item id="tab2">
			<Price></Price>
		</v-tab-item>
		<v-tab-item id="tab3">
			<Report></Report>
		</v-tab-item>
		<v-tabs-slider color="primary"></v-tabs-slider>

		<v-tab href="#tab1">
			<v-icon>list</v-icon>
		</v-tab>

		<v-tab href="#tab2">
			<v-icon>money</v-icon>
		</v-tab>
		<v-tab href="#tab3">
			<v-icon>bar_chart</v-icon>
		</v-tab>
	</v-tabs>
</template>

<script>
	import Home from './Home.vue'
	import Price from './Price.vue'
	import Report from './Report.vue'
	import eventbus from '../libs/eventbus'

	export default {
		components: {
			Home,
			Price,
			Report
		},
		data() {
			return {
				active: "tab1"
			}
		},
		watch: {
			active(value) {
				eventbus.$emit("view.show." + ({
					tab1: "Home",
					tab2: "Price",
					tab3: "Report"
				}[value]), value)
			}
		}
	}
</script>

<style lang="stylus">
	.v-tabs {
		height: 100%;
		width: 100%;
		>.v-tabs__bar {
			z-index: 1;
			position: fixed;
			width: 100%;
			bottom: 0;
			/*.v-tabs__slider-wrapper {
				top: 0;
				bottom: initial;
			}*/
		}
	}
	
	.v-window {
		height: 100%;
		>.v-window__container {
			height: 100%;
			>.v-window-item {
				height: 100%;
			}
		}
	}
</style>